body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#display-wrapper{grid-area:display;background:#333333EE;display:flex;align-items:center;justify-content:end;padding-right:1rem}#display-wrapper #display{font-size:2.5rem}

.number{background:#070707EE}#zero{grid-area:zero}#decimal{grid-area:decimal}.operation{background:#141414EE}#equals{grid-area:equals;background:#134369EE}#clear{grid-area:clear;background:#141414EE}

*{padding:0;margin:0;color:white;font-size:1.2rem;box-sizing:border-box}.grid-item{border:1px solid #222222}#page-wrapper{display:flex;height:100vh;justify-content:center;align-items:center;background:#222222}#page-wrapper #calculator-wrapper{display:grid;grid-template-columns:repeat(4, 100px);grid-template-rows:130px repeat(5, 100px);grid-template-areas:"display display display display" "clear clear divide multiply" "number number number subtract" "number number number add" "number number number equals" "zero zero decimal equals"}

